import { max, StrictMap } from "ecas-utils"

export interface AutomateOptions<T extends string> {
    /** Id of the parameter to modify, for example: pan, pitch, gain */
    paramId: T,
    /** A value curve that will be applied to the AudioParam */
    valueCurve: number[] | Float32Array | readonly number[] | Float32Array
    /** When the curve should start in consideration to AudioContext timer */
    when: number,
    /** How long it should take to reach the last value */
    duration: number
}

export type AutomatableParameters<T> = T extends Automatable<infer U> ? U extends readonly string[] ? U : never : never

export interface Automatable<T extends readonly string[]> {
    automator: Automator<T>
}

export class AutomatorError extends Error {
    override name = "AutomatorError"

    constructor(message?: string) {
        super(message)
    }
}

export class Automator<T extends readonly string[]> {
    AutomatableParameters: T
    constructor(automatableParameters: T) {
        this.AutomatableParameters = automatableParameters
    }
    /** Stores a reference to the id and audio parameter that can be automated */
    #audioParams = new StrictMap<T[number], AudioParam>()

    /** Stores a reference of the default value for said parameter */
    #defaults = new StrictMap<T[number], number>()

    automate({paramId, valueCurve, when, duration}: AutomateOptions<T[number]>) {
        /** Because I have run into some issues when doing cancelAtTime
         * and setValueCurve at exactly the same startTime */
        const safety = 0.5 as const
        const audioParam = this.#audioParams.get(paramId)
        audioParam?.cancelAndHoldAtTime(max(when - safety, 0))
        audioParam?.setValueCurveAtTime(valueCurve, when, duration)
    }

    add(paramId: T[number], audioParam: AudioParam, defaultValue?: number) {
        if (this.#audioParams.has(paramId)) {
            throw new AutomatorError(`AudioParam with id ${paramId} has already been added.`)
        }
        this.#audioParams.set(paramId, audioParam)
        this.#defaults.set(paramId, defaultValue || audioParam.value)
    }

    /** When you don't want to add a new param just update an existing one */
    set(paramId: T[number], audioParam: AudioParam, defaultValue?: number) {
        this.#audioParams.set(paramId, audioParam)
        this.#defaults.set(paramId, defaultValue || audioParam.value)
    }

    /** Cancel any planned values */
    cancel({paramId, when}: {paramId: T[number], when: number}) {
        const audioParam = this.#audioParams.get(paramId)
        audioParam?.cancelScheduledValues(when)
    }

    get(param: T[number]) {
        return this.#audioParams.get(param)
    }

    /** Get the current value of the parameter */
    current(param: T[number]) {
        return this.#audioParams.get(param).value
    }

    /** get the default value of said parameter */
    getDefault(param: T[number]) {
        return this.#defaults.get(param)
    }

    setDefault(param: T[number], value: number) {
        this.#defaults.set(param, value)
    }

    dispose() {
        this.#audioParams.clear()
    }
}
