import { Assert, isArray } from "./checks";
/* returns an array with no duplicate items */
export function unique(array) {
    return [...new Set(array)];
}
export function zip(a, b) {
    Assert(a.length === b.length, `zip arrays of unequal length`);
    return a.map((v, i) => [v, b[i]]);
}
export function* zipn(...toZip) {
    // Get iterators for all of the iterables.
    const iterators = toZip.map(i => i[Symbol.iterator]());
    while (true) {
        // Advance all of the iterators.
        const results = iterators.map(i => i.next());
        // If any of the iterators are done, we should stop.
        if (results.some(({ done }) => done)) {
            break;
        }
        // We can assert the yield type, since we know none
        // of the iterators are done.
        yield results.map(({ value }) => value);
    }
}
export function getFirstValueInNDimensionalArray(arr) {
    if (isArray(arr)) {
        return getFirstValueInNDimensionalArray(arr[0]);
    }
    return arr[0];
}
export function arraySum(arr) {
    let sum = 0;
    for (const v of arr) {
        sum += v;
    }
    return sum;
}
export function arrayAddMutate(arr, value) {
    for (let i = 0; i < arr.length; i++) {
        arr[i] += value;
    }
    return arr;
}
export function arrayPowerMutate(arr, pow = 2) {
    for (let i = 0; i < arr.length; i++) {
        arr[i] = Math.pow(arr[i], pow);
    }
    return arr;
}
export function arrayAdd(X, num) {
    return X.map(xn => xn + num);
}
export function arrayDifference(X, Y) {
    const diff = new Array(X.length);
    for (let i = 0; i < X.length; i++) {
        diff[i] = Y[i] - X[i];
    }
    return diff;
}
export function range(max) {
    return [...Array(max).keys()];
}
export function xrange(min, max) {
    return arrayAdd(range(min - max), min);
}
export function inxrange(min, max) {
    return zipn(xrange(min, max));
}
export function lastElement(arr) {
    return arr[arr.length - 1];
}
export function firstElement(arr) {
    return arr[0];
}
export function includes(arr, searchElement) {
    return Array.prototype.includes.call(arr, searchElement);
}
