export function tryAsync(func) {
    return async (...args) => {
        try {
            return [null, await func(...args)];
        }
        catch (err) {
            return [err, null];
        }
    };
}
export function trySync(func) {
    return (...args) => {
        try {
            return [null, func(...args)];
        }
        catch (err) {
            return [err, null];
        }
    };
}
